import conversations from "./conversations";
import state from "../core/state";
const getMoment = () => import("moment/src/moment");

import storage from "../util/storage";

class Chat {
  constructor() {
    this.conversation = new conversations();
    this.activeConversation = [];
    this.storage = new storage();
    getMoment().then((moment) => {
      if (moment === undefined) return;
      this.moment = new moment.default();
    });
  }

  /**
   * Send message
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by visitor
   */
  sendMessage(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.from = message;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send varied reponse
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendVariedMessage(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.information = message.information;
    obj.cases = message.state;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send phone number
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendPhoneNumber(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.from = message.phone_no;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send email
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendEmail(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.from = message.email;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send name
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendName(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.from = message.name;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send MCQ with image
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendImageWithMCQ(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.information = message.information;
    obj.cases = message.state;
    obj.imageMCQ = message.image;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send redirect URL
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendRedirectURL(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.redirect = message.Redirect_URL;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send video
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendVideo(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.from = message.video_info;
    obj.video = message.Video;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send input
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendInput(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.from = message.input;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send rest-dynamic-step-v2
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  sendRest(type, message, toOrFrom) {
    var obj = {};
    obj.to = toOrFrom ? toOrFrom : null;
    obj.from = message.url;
    // To save data in vuex
    this.saveInState(obj);
  }

  /**
   * Send message after delay
   * @param {string} type  Get active message type
   * @param {JSON} message  Get active flow JSON
   * @param {Number} toOrFrom  Message which is given by vistior
   */
  waitSendMessage(type, message, toOrFrom) {
    var obj = {};
    var days = message.days * 24 * 60 * 60;
    var hours = message.hours * 60 * 60;
    var minutes = message.minutes * 60;
    var milliSec = (days + hours + minutes) * 1000;
    return new Promise((resolve, reject) => {
      // Usage!
      this.sleep(milliSec, resolve).then(() => {
        obj.to = toOrFrom ? toOrFrom : null;
        obj.from = null;
        // To save data in vuex
        this.saveInState(obj);
        resolve("success");
      });
    });
  }

  // sleep time expects milliseconds
  sleep(time) {
    return new Promise((resolve) => setTimeout(resolve, time));
  }

  // send data into vuex and array
  saveInState(obj) {
    if (this.storage.getSessionStorage("chat") == "Inactive") {
      obj.from = "";
      delete obj.information;
      delete obj.cases;
    }

    // Format current date like MMM DD,YYYY
    obj.time = this.moment.format("MMM DD,YYYY hh:mm A");
    // To save data in vuex
    state.commit("setMessages", obj);

    // Object push into array of JSON
    this.activeConversation.push(obj);
  }
}
export default Chat;



