import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";

Vue.use(Vuex);

const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
  key: "botup",
});
const state = new Vuex.Store({
  plugins: [vuexLocal.plugin],
  namespaced: true,
  state: {
    name: {},
    conversations: [],
    conversationID: "",
    activeMessage: "",
    conversationHistory: [],
    botConfigurations: {},
    email: {},
    phoneNumber: {},
    data: [],
    execJson: [],
    activeConversation: [],
    chatStatus: true,
    generatedKey: "",
  },
  mutations: {
    // To set contact route generated key
    setGeneratedKey(state, payload) {
      state.generatedKey = payload;
    },
    // To set chat status
    setChatStatus(state, payload) {
      state.chatStatus = payload;
    },

    // To set active convesation
    setActiveConversation(state, payload) {
      state.activeConversation = payload;
    },
    // To set execute JSONs
    setExecJson(state, payload) {
      state.execJson = payload;
    },
    // To set bot inputs
    botConfig(state, botConfigurations) {
      state.botConfigurations = { ...botConfigurations };
    },

    // To set node data with key value pair
    setStateJSON(state, payload) {
      state.data.push(payload);
    },

    // To set name
    setName(state, payload) {
      state.name = { ...payload };
    },

    // To set email
    setEmail(state, payload) {
      state.email = { ...payload };
    },

    // To set phone number
    setPhoneNumber(state, payload) {
      state.phoneNumber = { ...payload };
    },

    // To set active conversation ID
    setActiveConversationID(state, ID) {
      state.conversationID = ID;
    },

    // To set active message while vistory reply
    setActiveMessage(state, activeMessage) {
      state.activeMessage = activeMessage;
    },

    // To set active conversation history
    setMessages(state, JSON) {
      state.conversations.push(JSON);
    },

    // To set all conversations history
    setConversationHistory(state, payload) {
      state.conversationHistory.push(payload);
    },

    // Remove active conversation
    removeActiveConversation(state, payload) {
      state.conversations = payload;
    },

    // Remove active conversation ID
    removeActiveConversationID(state, ID) {
      state.conversationID = ID;
    },

    // Remove admin inputs
    removeUserInputs(state, botConfigurations) {
      state.botConfigurations = { ...botConfigurations };
    },
  },
  getters: {
    // Get contact route generated key
    getGeneratedKey: (state) => {
      return state.generatedKey;
    },
    // Get chat status
    getChatStatus: (state) => {
      return state.chatStatus;
    },

    // Get active convesation
    getActiveConversation: (state) => {
      return state.activeConversation;
    },

    // Get Execute JSON
    getExecJson: (state) => {
      return state.execJson;
    },

    // Get bot inputs
    getBotConfig: (state) => {
      return state.botConfigurations;
    },

    // Get state JSON
    getStateJSON: (state) => {
      return state.data;
    },

    // Get name
    getName: (state) => {
      return state.name;
    },

    // Get email
    getEmail: (state) => {
      return state.email;
    },

    // Get phone number
    getPhoneNumber: (state) => {
      return state.phoneNumber;
    },

    // Get active conversation ID
    getActiveConversationID: (state) => {
      return state.conversationID;
    },

    // Get active message
    getActiveMessage: (state) => {
      return state.activeMessage;
    },

    // Get active conversation history
    getCurrentMessages: (state) => {
      return state.conversations;
    },

    // Get all conversation history
    getAllConversations: (state) => {
      return state.conversationHistory;
    },
  },
  modules: {},
});

export default state;



