class Storage {
  constructor() {
    this.arrayOfJson = [];
  }
  /**
   * Set session
   */
  setSessionStorage(key, value, expireTime = null) {
    let storage = {
      expireTime: expireTime ? expireTime + this.getTimestamp() : null,
      value,
    };

    return sessionStorage.setItem(key, JSON.stringify(storage));
  }

  /**
   * Get session
   */
  getSessionStorage(key) {
    let storage = JSON.parse(sessionStorage.getItem(key));
    if (!(storage && storage.expireTime)) {
      return storage ? storage.value : null;
    }

    return this.isExpired(storage.expireTime) ? this.removeItem(key, true) : storage.value;
  }

  /**
   * Set session
   */
  setLocalStorage(key, value, expireTime) {
    let storage = {
      expireTime: expireTime ? expireTime + this.getTimestamp() : null,
      value,
    };

    return localStorage.setItem(key, JSON.stringify(storage));
  }

  /**
   * Get session
   */
  getLocalStorage(key) {
    let storage = JSON.parse(localStorage.getItem(key));
    if (!(storage && storage.expireTime)) {
      return storage ? storage.value : null;
    }

    return this.isExpired(storage.expireTime) ? this.removeItem(key, false) : storage.value;
  }

  /**
   * Get current timestamp in seconds.
   */
  getTimestamp() {
    return Math.floor(new Date().getTime() / 1000);
  }

  /**
   * Return if an item is expired
   * @param {Number} expireTime
   */
  isExpired(expireTime) {
    if (!expireTime) return false;
    let timeLeft = expireTime - this.getTimestamp();

    // return if expired
    return timeLeft <= 0;
  }

  /**
   * Remove an item
   * @param {String} key
   * @param {String} is_session
   */
  removeItem(key, is_session = false) {
    return is_session ? sessionStorage.removeItem(key) : localStorage.removeItem(key);
  }

  appendKey(key, value) {
    // Merge all
    return Object.assign({ [key]: value });
  }

  appendArray(key, value) {
    return this.arrayOfJson.push({ [key]: value });
  }
}
export default Storage;



