import Storage from "../storage";

class Session {
  constructor() {
    this.storage = new Storage();

    // Browser array
    this.dataBrowser = [
      {
        string: navigator.userAgent,
        subString: "Edg",
        identity: "Edge",
        versionSearch: "Edg",
      },
      {
        string: navigator.userAgent,
        subString: "UCBrowser",
        identity: "UCBrowser",
      },
      {
        string: navigator.userAgent,
        subString: "XiaoMi/MiuiBrowser",
        identity: "MiuiBrowser",
      },
      {
        string: navigator.userAgent,
        subString: "OPR",
        identity: "Opermini",
        versionSearch: "OPR",
      },
      {
        string: navigator.userAgent,
        subString: "Chrome",
        identity: "Chrome",
      },
      {
        string: navigator.userAgent,
        subString: "OmniWeb",
        versionSearch: "OmniWeb/",
        identity: "OmniWeb",
      },
      {
        string: navigator.vendor,
        subString: "Apple",
        identity: "Safari",
        versionSearch: "Version",
      },
      {
        prop: window.opera,
        identity: "Opera",
      },
      {
        string: navigator.vendor,
        subString: "iCab",
        identity: "iCab",
      },
      {
        string: navigator.vendor,
        subString: "KDE",
        identity: "Konqueror",
      },
      {
        string: navigator.userAgent,
        subString: "Firefox",
        identity: "Firefox",
      },
      {
        string: navigator.vendor,
        subString: "Camino",
        identity: "Camino",
      },
      {
        // for newer Netscapes (6+)
        string: navigator.userAgent,
        subString: "Netscape",
        identity: "Netscape",
      },
      {
        // For IE11
        string: navigator.userAgent,
        match: /Trident.*rv[ :]*11\./,
        identity: "Explorer",
      },
      {
        string: navigator.userAgent,
        subString: "MSIE",
        identity: "Explorer",
      },
      {
        string: navigator.userAgent,
        match: /Mobile Safari\/([\d.]+)/,
        identity: "Mobile Safari",
        versionSearch: "/AppleWebKit/([d.]+)/",
      },
      {
        string: navigator.userAgent,
        subString: "Gecko",
        identity: "Mozilla",
        versionSearch: "rv",
      },
      {
        // for older Netscapes (4-)
        string: navigator.userAgent,
        subString: "Mozilla",
        identity: "Netscape",
        versionSearch: "Mozilla",
      },
    ];

    // OS array
    this.dataOS = [
      {
        string: navigator.platform,
        subString: "Win",
        identity: "Windows",
      },
      {
        string: navigator.platform,
        subString: "Mac",
        identity: "Mac",
      },
      {
        string: navigator.userAgent,
        match: /Android\s([0-9\.]*)/,
        subString: "Android",
        identity: "Android",
      },
      {
        string: navigator.userAgent,
        subString: "iPhone",
        identity: "iPhone/iPod",
      },
      {
        string: navigator.platform,
        subString: "Linux",
        identity: "Linux",
      },
    ];
  }
  /**
   * Generate UUID for session
   */
  getConversationId() {
    var dt = new Date().getTime();
    var uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, (c) => {
      var r = (dt + Math.random() * 16) % 16 | 0;
      dt = Math.floor(dt / 16);
      return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
    });
    return uuid;
  }

  /**
   * Get visitor information
   */
  getVisitorInfo() {
    if (this.storage.getSessionStorage(`visitorInfo`)) return this.storage.getSessionStorage(`visitorInfo`);

    // Stores visitor information
    var visitorInfo = {};

    // Get browser
    visitorInfo.browser = this.searchString(this.dataBrowser) || "An unknown browser";

    // Get browser version
    visitorInfo.version = this.searchVersion(navigator.userAgent) || this.searchVersion(navigator.appVersion) || this.searchMobileVersion(navigator.userAgent) || "An unknown version";

    // Get browser metadata like nAgent, nVersion[Number],majorVersion[String]
    visitorInfo.nVersion = navigator.appVersion;
    visitorInfo.nAgent = navigator.userAgent;
    visitorInfo.majorVersion = visitorInfo.version;

    // Get OS
    visitorInfo.OS = this.searchString(this.dataOS) || "An unknown OS";

    // Get device type
    visitorInfo.deviceType = this.getPlatformType();

    // Get device width
    visitorInfo.deviceWidth = screen.width;

    // Get device height
    visitorInfo.deviceHeight = screen.height;

    this.storage.setLocalStorage("visitorInfo", visitorInfo);

    return visitorInfo;
  }

  /**
   * Get browser and OS
   * @param {Object} data
   */
  searchString(data) {
    for (var i = 0; i < data.length; i++) {
      var dataString = data[i].string;
      var dataProp = data[i].prop;
      var match = data[i].match;
      this.versionSearchString = data[i].versionSearch || data[i].identity;

      if (match && dataString.match(match)) return data[i].identity;

      if (dataString) {
        if (dataString.indexOf(data[i].subString) != -1) return data[i].identity;
      } else if (dataProp) return data[i].identity;
    }
  }

  /**
   * Get browser version of all browsers except safari
   * @param {String} dataString
   */
  searchVersion(dataString) {
    var index = dataString.indexOf(this.versionSearchString);
    if (index == -1) return;
    return parseFloat(dataString.substring(index + this.versionSearchString.length + 1));
  }

  /**
   * Get version of safari browser
   * @param {String} dataString
   */
  searchMobileVersion(dataString) {
    try {
      match = dataString.match(/Mobile Safari\/([\d.]+)/);
      if (match) return parseFloat(match[1]);
    } catch (e) {
      console.log("Version Error", e);
    }
  }

  /**
   * To get the user device type
   */
  getPlatformType() {
    // Store device type
    let deviceType = "";
    if (navigator.userAgent.match(/mobile/i)) {
      return (deviceType = "Mobile");
    } else if (navigator.userAgent.match(/iPad|Android|Touch/i)) {
      return (deviceType = "Tablet");
    } else {
      return (deviceType = "Desktop");
    }
  }

  /**
   * Generate visitor ID
   * @param {function} callback
   */
  getVisitorID(callback) {
    !this.visitorID ? callback(this.getConversationId()) : callback(this.visitorID);
  }
}
export default Session;



