<template>
  <div>
    <div :class="`botup-${UIPrefs.widget}-comment-section-chat`" :style="UIPrefs.font_family.css">
      <div v-if="UIPrefs.widget == 'social'">
        <div v-if="conversations.to" :class="`botupchat-${UIPrefs.widget}-comment-section-replay`">
          <div :class="`botupchat-bg-reply-${UIPrefs.widget}`">
            <div class="profile-flex-end">
              <img src="https://infinity.500apps.com/avatars/avatar-57.png" class="social-avatar rounded-circle mr-2" width="20" height="20" />
              <div class="botupchat-small-2" v-if="conversations.time && loading">
                <vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" />
              </div>
              <div class="botupchat-small-2" v-if="conversations.time && !loading">
                <div class="sending">Sending...</div>
              </div>
            </div>
            <div v-html="conversations.to" class="margin-zero"></div>
          </div>
        </div>
        <div v-if="conversations.from && sleep" :class="`botupchat-${UIPrefs.widget}-message-box`">
          <div :class="`botupchat-bg-chat-${UIPrefs.widget}`">
            <div class="profile-flex">
              <div class="botupchat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
              <div class="profile-flex">
                <div class="botup-social-avatar-name">{{ UIPrefs.chat_header.attr.bot_name }}</div>
                <div><img :src="UIPrefs.chat_header.attr.gravatar" class="social-avatar rounded-circle mr-2" width="20" /></div>
              </div>
            </div>
            <div class="margin-zero">{{ conversations.from }}</div>
          </div>
        </div>
      </div>
      <div v-else>
        <div v-if="conversations.to" :class="`botupchat-${UIPrefs.widget}-comment-section-replay`">
          <div :class="`botupchat-bg-reply-${UIPrefs.widget}`">
            <div v-html="conversations.to" class="margin-zero"></div>
          </div>
          <div class="botupchat-small-2" v-if="conversations.time && loading"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
          <div class="botupchat-small-2" v-if="conversations.time && !loading">
            <div class="sending">Sending...</div>
          </div>
        </div>
        <div v-if="conversations.from && sleep" :class="`botupchat-${UIPrefs.widget}-message-box`">
          <div class="botup-avatar-flex">
            <div v-if="UIPrefs.widget == 'ultra'" class="align-self-end">
              <img :src="UIPrefs.chat_header.attr.gravatar" class="rounded-circle mr-2 rounded-avatar" width="24" />
            </div>
            <div :class="`botupchat-bg-chat-${UIPrefs.widget}`">
              <div class="margin-zero">{{ conversations.from }}</div>
            </div>
          </div>
          <div class="botupchat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
        </div>
      </div>
      <div v-if="conversations.image && sleep" :class="`botupchat-${UIPrefs.widget}-message-box`">
        <div>
          <img :src="conversations.image" class="rounded-circle" />
        </div>
        <div class="botupchat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
      </div>
      <div v-if="conversations.video && sleep" :class="`botupchat-${UIPrefs.widget}-message-box`">
        <div :class="`botupchat-bg-chat-${UIPrefs.widget}`">
          <a :href="conversations.video" target="_blank" class="mcq-video-link">
            {{ conversations.video }}
          </a>
        </div>
        <div class="botupchat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
      </div>
      <div v-if="conversations.redirect && sleep" :class="`botupchat-${UIPrefs.widget}-message-box`">
        <div :class="`botupchat-bg-chat-${UIPrefs.widget}`">
          <a :href="conversations.redirect" target="_blank" class="mcq-video-link small">
            {{ conversations.redirect }}
          </a>
        </div>
        <div class="botupchat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
      </div>
      <div v-if="conversations.cases && sleep" :class="`botupchat-${UIPrefs.widget}-comment-section-chat`">
        <div class="botupchat-mcq" :style="UIPrefs.font_family.css">
          <div class="faq-margin-zero">{{ conversations.information }}</div>
          <img v-if="conversations.imageMCQ" :src="conversations.imageMCQ" class="mcq-img" />
          <div v-for="(value, index) in conversations.cases" :key="index">
            <button pill class="botupchat-btn-light" :style="UIPrefs.font_family.css" :ref="`MCQ-${Object.values(value)[0]}-${index}`" v-on:click="emit_MCQ_visitor_response(value, index)">{{ Object.keys(value)[0] }}</button>
          </div>
        </div>
        <div class="botupchat-small-2" v-if="conversations.time"><vue-moments-ago prefix="" suffix="ago" :date="conversations.time" lang="en" /></div>
        <div class="botupchat-small-2" v-if="conversations.time && !loading">
          <div class="sending">Sending...</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    components: { VueMomentsAgo: () => import("vue-moments-ago") },
    props: ["conversations"],
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
        sleep: false,
        loading: false,
      };
    },
    async mounted() {
      // set tone while user replay
      if (window.refresh_loading) {
        // Sleep time expects milliseconds for user
        this.waitMessage(true);

        // Sleep time expects milliseconds for bot
        this.waitMessage(false);
      } else {
        this.loading = true;
        this.sleep = true;
      }
    },
    methods: {
      /**
       * Trigger when user select MCQ questions
       * @param {Object} value  Get respective obj value
       * @param {Number} id  Get index
       */
      async emit_MCQ_visitor_response(value, id) {
        window.refresh_loading = true;
        // Set tone while user trigger MCQ questions
        window._Botup.UIPrefs.play(this.UIPrefs.chat.sound.playMessageOut);

        this.loading = false;
        // Remove element based on id
        this.conversations.cases.filter((item, index) => {
          if (index !== id) this.$refs[`MCQ-${Object.values(item)[0]}-${index}`][0].remove();
        });

        // Set the count based on id which is selected by visitor
        var MCQ_count = id == 0 ? this.conversations.cases.length - 1 : this.conversations.cases;

        // Save the active message in state
        window._Botup.Bot.saveInState("setActiveMessage", value);
        await new Promise((resolve) => setTimeout(resolve, 1500));
        this.loading = true;
      },

      // Automatically scroll down while updating conversation
      scrollToEnd() {
        setTimeout(() => {
          const container = document.getElementById("botupchat");
          if (container) container.scrollTop = container.scrollHeight;
        }, 0);
      },

      /**
       * Sleep time expects milliseconds for bot
       * @param {Boolean} check validation by user or bot
       */
      async waitMessage(check) {
        await window._Botup.Chat.sleep(1500).then((res) => {
          check ? (this.loading = true) : (this.sleep = true), window._Botup.UIPrefs.play(this.UIPrefs.chat.sound.playMessageIn);
          // Automatically scroll down in onload
          this.scrollToEnd();
        });
      },
    },
  };
</script>



