import dataJS from "../util/data";
import storage from "../util/storage";
import state from "../core/state";
import store from "./store";
class Bot {
  constructor() {
    this.selectedMCQ = [];
    this.storage = new storage();
    this.store = new store();
    var currentPage = window.location.href;
    var lastURL = this.storage.getSessionStorage("lastURL");
    // New page loaded
    if (lastURL == null || currentPage !== lastURL) return this.storage.setSessionStorage("lastURL", currentPage);
    // we need to set up flag for execute next step when refreshed page
    this.refreshFlag = false;
  }

  // Receive message from vuex
  receiveMessage() {
    this.stateJSON = null;
    this.refreshFlag = false;
    if (this.storage.getSessionStorage("chat") == "Inactive") {
      this.storage.setSessionStorage("chat", "Active");
      return new Promise((resolve, reject) => {
        state.watch((state) => {
          state.activeMessage, (this.stateJSON = typeof state.activeMessage == "object" ? null : state.activeMessage);
          resolve(state.activeMessage);
        });
      });
    } else
      return new Promise((resolve, reject) => {
        state.watch(
          (state) => state.activeMessage,
          (oldValue) => {
            // If MCQ then null value should be assigned else direct message then watcher value should be assigned to variable
            this.stateJSON = typeof oldValue == "object" ? null : oldValue;
            resolve(oldValue);
          },
        );
      });
  }

  // Validate email
  validateEmail(email) {
    // To remove the tags by input editor
    email = email.replace(/( |<([^>]+)>)/gi, "");
    var regex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
    return regex.test(email);
  }

  // Check visitor response valid or not based on MCQ's
  validateMCQCases(mcqCases, visitorResponse) {
    if (typeof visitorResponse == "object") return true;
    this.selectedMCQ = mcqCases.filter((cases) => {
      if (Object.keys(cases)[0].indexOf(visitorResponse.replace(/( |<([^>]+)>)/gi, "")) == 0) return cases;
    });
    return this.selectedMCQ.length ? true : false;
  }

  // To store into vuex
  saveInState(mutation, payload) {
    state.commit(mutation, payload);
  }

  /**
   * To get flows
   * @param {Number} flowID  Get flow info based on ID
   */
  async getBot(flowID) {
    if (this.storage.getSessionStorage(`flowID-${flowID}`)) return this.storage.getSessionStorage(`flowID-${flowID}`);
    var data = new dataJS("https://botup.${region}.500apps.com/botup/flows/" + flowID);
    var response = await data.get();
    console.log("response===>", response);
    // To set project id in local storage
    this.storage.setLocalStorage(`projectID-${flowID}`, response.project_id);
    this.storage.setSessionStorage(`flowID-${flowID}`, JSON.parse(response.workflow));
    return JSON.parse(response.workflow);
  }

  /**
   * Execution Starts
   * @param {JSON} stepId  active step ID
   * @param {Number} botJson  call flow JSON
   * @param {Number} stateJSON  Message which is given by visitor
   */
  async executeFlow(stepId, botJson, stateJSON) {
    // To get key when visitor triggers MCQ Questions
    this.stateJSON = typeof stateJSON == "object" ? Object.keys(stateJSON)[0] : stateJSON;

    // Format valid JSON
    this.json = this.transformJSON(botJson);
    this.saveInState("setExecJson", this.json);

    // Read step from local storage if this is executing again
    if (!stepId) stepId = await this.storage.getLocalStorage(`botup-${window.flowID}`);

    // To check node type
    stepId = stepId ? stepId : "start";
    var type = stepId == "start" ? "type" : "id";

    // Find out active step JSON
    var startStep = this.json.find((item) => {
      return item[type] === stepId;
    });

    // Execute through by next ID
    !this.refreshFlag ? this.exec(startStep.next) : this.execEachStep(startStep);
  }

  /**
   * Execeute next step ID and return json
   * @param {Number} step  Invoke current step ID
   */
  exec(step) {
    this.json = !this.json ? window._Botup.State.getters.getExecJson : this.json;
    var step = this.json.find((item) => {
      return item.id === step;
    });

    // Hangup executes only one time
    if (!step) return this.execStep({ id: "hangup", type: "hangup" });

    /**
     * Handlebars will compile automatically with all the keys
     * @param {Object} step  Invoke current step JSON
     * @param {Object} stateJSON  Invoke visitor response
     */
    step = this.executeHB(step, window._Botup.State.getters.getStateJSON);

    // Execute each step
    this.execEachStep(step);
  }

  // Execute each step based on step ID
  async execEachStep(step) {
    // Execute
    this.execStep(step).then((value) => {
      // Next step id
      this.exec(value ? (typeof value == "string" ? step.next : Object.values(value)[0]) : step.next);
    });
  }

  /**
   * Flow executes based on step
   * @param {JSON} step  Invoke JSON object
   */
  async execStep(step) {
    let response;
    // Execute callback function
    if (this.refreshFlag) return (response = await this.receiveMessage());

    // Store the current step
    await this.storage.setLocalStorage(`botup-${window.flowID}`, step.id);

    switch (step.type) {
      case "Message":
        response = window._Botup.Chat.sendMessage(step.type, step.message, this.stateJSON);
        this.stateJSON = null;
        break;
      case "Varied Responses":
        window._Botup.Chat.sendVariedMessage(step.type, step, this.stateJSON);
        while (true) {
          // Wait for response
          response = await this.receiveMessage();
          // validate email
          if (this.validateMCQCases(step.state, response)) {
            // Visitor send message directly without trigger MCQ option
            response = typeof response == "object" ? response : this.selectedMCQ[0];
            break;
          } else window._Botup.Chat.sendVariedMessage(step.type, step, response);
        }
        break;
      case "Get Phone Number":
        window._Botup.Chat.sendPhoneNumber(step.type, step, this.stateJSON);
        // Wait for response
        response = await this.receiveMessage();
        // Create object with step output from designer JSON & save data in state
        this.saveInState("setPhoneNumber", { [step.Output]: response });
        // Save into state JSON
        this.saveInState("setStateJSON", { [step.Output]: response });
        break;
      case "Get Email":
        window._Botup.Chat.sendEmail(step.type, step, this.stateJSON);
        while (true) {
          // Wait for response
          response = await this.receiveMessage();
          // validate email
          if (this.validateEmail(response)) break;
          else window._Botup.Chat.sendMessage("", "Enter a valid email", response);
        }
        // Create object with step output from designer JSON & save data in state
        this.saveInState("setEmail", { [step.Output]: response });
        // Save into state JSON
        this.saveInState("setStateJSON", { [step.Output]: response });
        break;
      case "Get Name":
        window._Botup.Chat.sendName(step.type, step, this.stateJSON);
        response = await this.receiveMessage();
        // Create object with step output from designer JSON & save data in state
        this.saveInState("setName", { [step.Output]: response });
        // Save into state JSON
        this.saveInState("setStateJSON", { [step.Output]: response });
        break;
      case "Image With MCQ":
        window._Botup.Chat.sendImageWithMCQ(step.type, step, this.stateJSON);
        while (true) {
          // Wait for response
          response = await this.receiveMessage();
          // validate email
          if (this.validateMCQCases(step.state, response)) {
            // Visitor send message directly without trigger MCQ option
            response = typeof response == "object" ? response : this.selectedMCQ[0];
            break;
          } else window._Botup.Chat.sendImageWithMCQ(step.type, step, response);
        }
        break;
      case "Redirect URL":
        response = window._Botup.Chat.sendRedirectURL(step.type, step, null);
        break;
      case "Video":
        response = window._Botup.Chat.sendVideo(step.type, step, null);
        break;
      case "delay":
        response = await window._Botup.Chat.waitSendMessage(step.type, step, null);
        break;
      case "Get Input":
        window._Botup.Chat.sendInput(step.type, step, this.stateJSON);
        response = await this.receiveMessage();
        // Create object with step output from designer JSON & save data in state
        this.saveInState("setStateJSON", { [step.Output]: response });
        break;
      case "rest-dynamic-step-v2":
        response = window._Botup.Chat.sendRest(step.type, step, null);
        break;
      case "JS code":
        response = this.execJSCode(step.code, window._Botup.State.getters.getStateJSON);
        break;
      case "hangup":
        // To store visitor details
        if (!window._Botup.State.getters.getGeneratedKey) await this.store.contactInfo();

        // To store conversation in state
        this.store.conversationHistory();

        // Send bot has end the chat
        window._Botup.Chat.sendMessage("", "Thanks for using our bot. If you like this experience, get one of your own for your website at Botup.com by 500apps.", this.stateJSON);

        this.storage.setLocalStorage(`botup-${window.flowID}`, undefined);
        break;
      default:
        return new Promise((resolve, reject) => {
          resolve();
        });
    }

    return response;
  }

  /**
   * Executes javascript code. Data is resected to the data returned from function.
   * Ensure that the javascript function returns the original data also and NOT just the changed attributes.
   * @param {Object} code - Properties of the node in the workflow json
   * @param {Object} stateJSON - Visitor response
   */
  execJSCode(code, stateJSON) {
    var map = {};
    stateJSON.forEach((item) => {
      map[Object.keys(item)[0]] = item[Object.keys(item)[0]].replace(/( |<([^>]+)>)/gi, "");
    });
    var scope = this;
    var JSFunction = eval("(" + code + ")");
    var response = JSFunction(stateJSON, scope);
    if (!response) return "success";
  }

  /**
   * Compiles the keys in object with HB
   * @param {Object} source - Object (template to compile)
   * @param {String} stateJSON - stateJSON to compile with
   */
  executeHB(source, stateJSON) {
    var scope = this;
    // Apply handlebars on each key/value pairs
    Object.keys(source).forEach(function(keyValue, index) {
      // Skip if the key is an object. We should reiterate
      if (typeof source[keyValue] != "object") {
        var value = source[keyValue];
        if (!value) return;
        source = scope.compileToHB(source, stateJSON, value, index, keyValue);
        // Check MCQ's & compile keys with state JSON
        if (!source.hasOwnProperty("cases")) return;
        source.cases.forEach(function(sourceValue, index) {
          source = scope.compileToHB(source, stateJSON, Object.values(sourceValue)[0], index, sourceValue);
        });
      }
    });
    return source;
  }

  /**
   * To remove Quill tags & form object
   * @param {Object} state visitor response
   */
  formatObject(state) {
    return { [Object.keys(state)[0]]: Object.values(state)[0].replace(/( |<([^>]+)>)/gi, "") };
  }

  /**
   * Handlebars compile all the data
   * @param {Object} source Object (template to compile)
   * @param {Array} stateJSON Visitor response
   * @param {String} value Each state JSON value
   * @param {Number} index Cases index value
   * @param {String} keyValue Object key
   */
  compileToHB(source, stateJSON, value, index, keyValue) {
    const Handlebars = require("handlebars");
    var template = Handlebars.compile(value);
    var data = {};
    stateJSON.forEach((item) => {
      item = this.formatObject(item);
      data[Object.keys(item)] = item[Object.keys(item)];
    });
    value = template(data);
    // We will compile again as the key may also be handlebars
    template = Handlebars.compile(value);
    if (value != source[keyValue]) !source.hasOwnProperty("cases") ? (source[keyValue] = value) : (source.state[index] = { [value]: Object.values(source.state[index])[0] });
    return source;
  }

  // Transform the JSON into simple json
  transformJSON(apps) {
    return apps.nodes.map((item) => {
      // JSON Values - make them as name:value
      const jsonValues = item.JsonValues.map((item) => {
        let obj = {};

        if (item.hasOwnProperty("name")) {
          obj[item.name] = item.value;
        } else {
          // For Grid kind of JsonValues
          var key = Object.keys(item)[0];
          obj[key] = item[key];
        }

        return obj;
      });

      // Reduce Array
      const reducer = (accumulator, currentValue) => Object.assign(accumulator, currentValue);
      const jsonKeys = jsonValues.reduce(reducer, {});
      // Merge all
      return Object.assign(
        {
          type: item.NodeDefinition.type ? item.NodeDefinition.type.name : item.NodeDefinition.name,
          id: item.id,
          next: item.States[0].success ? item.States[0].success : item.States[0].start,
          state: item.States,
        },
        jsonKeys,
      );
    });
  }
}
export default Bot;



