<template>
  <!-- chat bot -->
  <div class="chat_icon" v-if="showIcon">
    <a id="popover-button-sync" v-on:click="statusMode()" variant="link">
      <img v-if="!active" :src="icon" class="botup-icon chat" />
      <img v-if="active" :src="icon" class="botup-icon arrow" />
    </a>
  </div>
  <!-- chot end -->
</template>
<script>
  export default {
    data() {
      return {
        showIcon: true,
        chat: false,
        active: false,
        minimized: false,
        unreadMessages: false,
        icon: window._Botup.UIPrefs.prefs.chat_icon.attr.chat_image,
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    mounted() {},
    watch: {
      "$route.params.active"(active) {
        this.$nextTick(() => {
          // New page loaded
          window._Botup.Bot.storage.setSessionStorage("lastURL", window.location.href);
          // Set icon
          if (active) this.icon = window._Botup.UIPrefs.prefs.chat_icon.attr.active_image;
          else this.icon = window._Botup.UIPrefs.prefs.chat_icon.attr.chat_image;
        });
      },
    },
    methods: {
      statusMode() {
        if (this.$route.name == "chat") {
          this.active = false;
          // Hide the chat
          this.$parent.show = false;
          this.$router.push({
            name: "icon",
            params: { active: this.active },
          });
          // To set chat status in vuex while chat end session
          window._Botup.State.commit("setChatStatus", false);
        } else {
          // Show the chat
          window.refresh_loading = false;
          this.$parent.show = true;
          this.active = true;
          this.$router.push({
            name: "chat",
            params: { active: this.active },
          });
          // To set chat status in vuex while chat end session
          window._Botup.State.commit("setChatStatus", true);
        }
      },
    },
  };
</script>



