import Vue from "vue";
import VueRouter from "vue-router";
import welcome from "../views/Welcome.vue";
import chat from "../views/Window.vue";
import conversations from "../views/Conversations.vue";
import icon from "../views/Icon.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "welcome",
    component: welcome,
  },
  {
    path: "/welcome",
    name: "welcome",
    component: welcome,
  },
  {
    path: "/chat",
    name: "chat",
    component: chat,
  },
  {
    path: "/conversations",
    name: "conversations",
    component: conversations,
  },
  {
    path: "/icon",
    name: "icon",
    component: icon,
    props: true,
  },
];

export default new VueRouter({
  routes,
  mode: "abstract",
  linkActiveClass: "active",
});



