<template>
  <div id="app" class="botup-chat" :style="UIPrefs.chat_width.css">
    <div>
      <Header :conversationHeader="true"></Header>
      <div class="converstion-min-height">
        <div v-if="!conversationList.length">
          <div class="bg-white mx-auto pb-3 text-center empty-state">
            <img src="https://infinity.500apps.com/img/botup/bot-history-empty-state.svg" width="200" class="img-fluid mb-2" />
            <p class="d-block text-black-50">
              Your chats have not been tracked yet
            </p>
          </div>
        </div>
        <!-- user bot list starts here -->
        <div v-else>
          <row class="ml-0 mr-0 pb-1" v-if="conversationList.length">
            <card no-body class="w-100 border-0 p-0">
              <card-body class="my-n3 o-cardbody">
                <div class="border-bottom">
                  <div class="m-0 pb-2 pl-0 pr-0 pt-2 mt-1 conv-border-bottom" v-for="(item, index) in conversationList" v-bind:key="index">
                    <row class="align-items-top convlist">
                      <div>
                        <div class="text-uppercase">
                          <img :src="UIPrefs.chat_header.attr.gravatar" class="convlist-img" />
                        </div>
                      </div>
                      <div>
                        <h4 class="mb-0 ellipsis heading-text text-capitalize" @click="conversations(item, index)">
                          {{ UIPrefs.chat_header.attr.bot_name }}
                        </h4>
                        <p class="datetime small lineheight text-muted">
                          <vue-moments-ago prefix="" suffix="ago" :date="item[conversationID][index].time" lang="en" />
                        </p>
                      </div>
                    </row>
                  </div>
                </div>
              </card-body>
            </card>
          </row>
          <!-- end -->
        </div>
      </div>
      <Footer :conversationFooter="true"></Footer>
    </div>
  </div>
</template>
<script>
  import Header from "./Window/Header.vue";
  import Footer from "./Window/Footer.vue";

  export default {
    components: { Header, Footer, VueMomentsAgo: () => import("vue-moments-ago") },
    data() {
      return {
        conversationList: [],
        conversationID: "",
        UIPrefs: window._Botup.UIPrefs.prefs,
        conversationID: "",
      };
    },
    created() {},
    mounted() {
      this.conversationList = window._Botup.State.getters.getAllConversations;
      // Get active conversation ID
      this.conversationID = window._Botup.State.getters.getActiveConversationID;
    },
    methods: {
      // Get the details with respective subject
      conversations(value, ind) {
        window.refresh_loading = false;
        // To remove active conversation from vuex
        window._Botup.State.commit("removeActiveConversation", []);

        // To set active conversation ID which is selected from history by visitor
        window._Botup.Chat.conversation.setActiveConversationID(Object.keys(value)[0]);

        var result = this.conversationList[ind];

        // To set active conversation
        result[this.conversationID].forEach((obj) => {
          window._Botup.State.commit("setMessages", obj);
        });

        // Redirects to chat
        this.$router.push({
          name: "chat",
          params: { active: true },
        });

        // To set chat status in vuex while chat end session
        window._Botup.State.commit("setChatStatus", true);
      },
    },
  };
</script>



