const PCors = "https://my.${region}.500apps.com/pcors";

class Data {
  constructor(url, jwt, params) {
    // eslint-disable-next-line
    let region = this.getRegion();
    this.url = eval("`" + url + "`");
    this.pcorsURL = eval("`" + PCors + "`");

    if (params) this.params = params;

    this.pcors = true;

    this.headers = {};

    this.offset = 0;
    this.limit = 50;

    // Enable Offset
    this.enableOffset = false;
  }

  /**
   * Function to make rest calls using axios API
   * @param {string} url    # url to make a call
   */
  async get() {
    return new Promise(async (resolve, reject) => {
      await fetch(this.getPCors(this.url), {
        method: "GET",
        headers: this.getConfig().headers,
      })
        .then(function(response) {
          return response.json();
        })
        .then(function(data) {
          resolve(data);
        });
    });
  }

  /**
   * Function to make POST call using axios API
   * @param {string} url    # url to make a call
   */
  async add(form, is_append_data) {
    return new Promise(async (resolve, reject) => {
      await fetch(this.getPCors(this.getPCors()), {
        method: "POST",
        headers: this.getConfig().headers,
        body: JSON.stringify(form),
      })
        .then(function(response) {
          return response.json();
        })
        .then(function(data) {
          resolve(data);
        });
    });
  }

  // Get bearer token
  getConfig() {
    return {
      headers: {
        ...{
          "x-api-key": window.apiKey,
          "content-type": "application/json",
          withCredentials: true,
        },
        ...this.getHeaders(),
      },
    };
  }

  // Get headers
  getHeaders() {
    if (Object.keys(this.headers).length) return this.headers;
    return {};
  }

  // Append PCORS url
  getPCors(url) {
    if (!url) url = this.url;

    // Return URL directly if pcors is disabled
    if (!this.pcors) return url;

    // Use base url if not present - detail URL eg: /flow/{id} can overwrite
    return `${this.pcorsURL}?url=${encodeURIComponent(url)}`;
  }

  // GET region
  getRegion() {
    // This will always return a region
    return window.region;
  }

  // To append conditions with given url
  append(url, params) {
    if (!url) url = "";
    let prepend = "?";
    if (url.indexOf("?") != -1) prepend = "&";

    url = `${url}${prepend}${params}`;
    return url;
  }
}
export default Data;



