<template>
  <div id="app">
    <div class="botup-chat" :style="UIPrefs.chat_width.css">
      <Header></Header>
      <div class="botup-min-height-welcome">
        <div class="botup-welcome-box" :style="UIPrefs.chat_body.css">
          <div class="botup-welcome-bg">
            <div class="botup-welcome-message">
              {{ UIPrefs.chat_header.attr.bot_welcome_msg }}
              <img :src="UIPrefs.chat_header.attr.welcome_img" height="20" />
            </div>
          </div>
        </div>
      </div>
      <InputEditor :enableChat="true" @click="router()"></InputEditor>
      <Footer></Footer>
    </div>
  </div>
</template>
<script>
  import Header from "./Window/Header.vue";
  import Footer from "./Window/Footer.vue";
  import InputEditor from "./Window/InputEditor.vue";
  export default {
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    components: {
      Header,
      InputEditor,
      Footer,
    },
    mounted() {},
    created() {},
    methods: {
      // Redirects to chat while visitor trigger input editor
      router() {
        this.$router.push({
          name: "chat",
          params: { active: true },
        });
        // To set chat status in vuex while chat end session
        window._Botup.State.commit("setChatStatus", true);
      },
    },
  };
</script>



