<template>
  <div class="botup-header-profile-section" :style="UIPrefs.chat_header.css">
    <div class="botup-profile-pic-section">
      <div class="botup-profile-flex">
        <img v-if="!conversationHeader" :src="UIPrefs.chat_header.attr.gravatar" class="botup-gravatar" />
        <div v-else @click="navigate" style="cursor:pointer;">
          <img src="https://infinity.500apps.com/img/botup/chevron-left.svg" class="botup-chevron-left" />
        </div>
        <div class="botupchat-margin-left" style="cursor:pointer;" @click="minimize()">
          <div v-if="!conversationHeader">
            <div class="botup-bot-name">{{ UIPrefs.chat_header.attr.bot_name }}</div>
            <div class="botup-bot-description">{{ UIPrefs.chat_header.attr.bot_description }}</div>
          </div>
          <div v-else class="botup-bot-conv-name">Conversation History</div>
        </div>
      </div>
      <div class="botup-history-icon-bg" v-if="!conversationHeader">
        <button class="botup-widget-history" aria-label="Open conversation history" @click="viewHistory()">
          <img src="https://infinity.500apps.com/img/botup/history-icon.svg" class="botup-history-icon" />
        </button>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "Header",
    components: {},
    props: {
      conversationHeader: Boolean,
    },
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    mounted() {
      // Hide chat widget when user click on header
      document.addEventListener("click", (event) => {
        if (typeof event.target.className == "string" && event.target.className.includes("profile")) return this.minimize();
      });
    },
    methods: {
      minimize() {
        this.$router.push({
          name: "icon",
          params: { active: false },
        });
        // To set chat status in vuex while chat end session
        window._Botup.State.commit("setChatStatus", false);
      },

      // Redirect to conversation details view
      viewHistory() {
        if (window._Botup.State.getters.getActiveConversation.length == 0) window._Botup.State.commit("setActiveConversation", window._Botup.State.getters.getCurrentMessages);
        this.$router.push("/conversations");
      },

      // Navigate to active conversation view
      navigate() {
        var result = window._Botup.State.getters.getActiveConversation;
        // To set active conversation
        result.forEach((obj) => {
          window._Botup.State.commit("setMessages", obj);
        });
        window._Botup.State.commit("setActiveConversation", []);
        this.$router.push({
          name: "chat",
          params: { active: true },
        });
        // To set chat status in vuex while chat end session
        window._Botup.State.commit("setChatStatus", true);
      },
    },
  };
</script>



