<template>
  <div>
    <div class="botupchat-footer-500app" :style="UIPrefs.font_family.css">
      <div class="botupchat-items-center mb-0" v-if="!conversationFooter">Botup by<a class="botup-500app-link" rel="nofollow" @click="redirectTo500apps()">500apps</a></div>
      <div class="botupchat-items-center mb-0 pt-1" v-else>Botup by<a class="botup-500app-link" rel="nofollow" @click="redirectTo500apps()">500apps</a></div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      conversationFooter: Boolean,
    },
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    methods: {
      redirectTo500apps() {
        window.open("https://500apps.com");
      },
    },
  };
</script>



