import { store } from "./js/core/state";

import Botup from "./js/Botup";
window._Botup = new Botup();

import Vue from "vue";
import router from "./router";
import App from "./Main.vue";

Vue.config.productionTip = false;

new Vue({
  el: "#app",
  router,
  store,
  render: (h) => h(App),
});



