<template>
  <div id="app" :class="`botup-${UIPrefs.widget} cleanslate`" :style="UIPrefs.font_family.css">
    <Icon ref="icon"></Icon>
    <div class="main-content">
      <transition :name="UIPrefs.chat.attr.transition" ref="transition">
        <router-view v-if="show" />
      </transition>
    </div>
  </div>
</template>

<script>
  import Icon from "./views/Icon";
  export default {
    name: "app",
    components: {
      Icon,
    },
    data() {
      return {
        show: true,
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    mounted() {
      // Default set cleanslate css
      window._Botup.UIPrefs.loadCSS("https://widgets.500apps.com/botup/CSS/cleanslate.css", "500apps-cleanslate");

      // Font style css
      window._Botup.UIPrefs.loadCSS("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400;1,500&display=swap", "500apps-fonts");

      // By default chat should be inactive status in onload
      var self = this;
      this.$refs.icon.showIcon = false;
      setTimeout(() => {
        if (document.readyState == "complete") self.showChat();
      }, 1000);
    },
    methods: {
      // Set chat status
      showChat() {
        this.$refs.icon.showIcon = true;
        // By default chat should be inactive status
        if (window._Botup.State.getters.getChatStatus) return this.$refs.icon.statusMode();

        // Chat should be inactive while user end chat session
        this.$router.push({
          name: "/icon",
          params: { active: false },
        });
        // To set chat status in vuex while chat end session
        window._Botup.State.commit("setChatStatus", false);
      },
    },
  };
</script>
<style>
  /* Animation effects */
  .botup-animation-bounce-enter-active {
    animation: bounce-in 2s ease;
  }
  .botup-animation-bounce-leave-active {
    animation: bounce-in 0.9s;
  }
  @keyframes bounce-in {
    0% {
      opacity: 0;
      transform: scale(0.3);
    }
    50% {
      opacity: 1;
      transform: scale(1.05);
    }
    70% {
      transform: scale(0.9);
    }
    100% {
      transform: scale(1);
    }
  }
  .botup-animation-fade-enter-active,
  .botup-animation-fade-leave-active {
    transition: opacity 0.5s;
    animation: fade-in 2s linear;
  }
  .botup-animation-fade-enter,
  .botup-animation-fade-leave-to {
    opacity: 0;
  }
  @keyframes fade-in {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  .botup-animation-slide-enter-active,
  .botup-animation-slide-leave-active {
    transition: opacity 0.5s;
  }
  .botup-animation-slide-enter,
  .botup-animation-slide-leave-to {
    transform: scaleY(0);
  }
  /* Enter and leave animations can use different */
  /* durations and timing functions.              */
  .botup-animation-slide-fade-enter-active {
    transition: all 0.3s ease;
    animation: bounce-in-right 2s ease;
  }
  .botup-animation-slide-fade-leave-active {
    transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
  }
  .botup-animation-slide-fade-enter, .botup-animation-slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
    transform: translateX(0px);
    opacity: 0;
  }
  @keyframes bounce-in-right {
    0% {
      opacity: 0;
      transform: translateX(50px);
    }
    60% {
      opacity: 1;
      transform: translateX(-30px);
    }
    80% {
      transform: translateX(30px);
    }
    100% {
      transform: translateX(10px);
    }
  }
  .botup-animation-fade-in-left-enter-active {
    transition: all 0.3s ease;
    animation: fade-in-left 1s ease;
  }
  @keyframes fade-in-left {
    0% {
      opacity: 0;
      transform: translateX(-100px);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
  .botup-animation-fade-in-top-enter-active {
    transition: all 0.3s ease;
    animation: fade-in-top 1s ease;
  }
  @keyframes fade-in-top {
    from {
      opacity: 0;
      transform: translateY(-200px);
    }
    to {
      opacity: 1;
    }
  }
  .botup-animation-fade-in-up-enter-active {
    transition: all 0.3s ease;
    animation: fade-in-up 1s ease;
  }
  .botup-animation-fade-in-up-leave-active {
    transition: all 1s ease;
  }
  .botup-animation-fade-in-up-enter,
  .botup-animation-fade-in-up-leave-to {
    transform: translate3d(0, 10%, 0);
    opacity: 0;
  }
  @keyframes fade-in-up {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, 10%, 0);
      transform: translate3d(0, 10%, 0);
    }
    to {
      opacity: 1;
    }
  }
  .botup-animation-shake-enter-active {
    transition: all 0.3s ease;
    animation: shake 1s ease;
  }
  @keyframes shake {
    0%,
    100% {
      transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      transform: translateX(-10px);
    }
    20%,
    40%,
    60%,
    80% {
      transform: translateX(10px);
    }
  }
  .botup-animation-pulse-enter-active {
    transition: all 0.3s ease;
    animation: pulse 1s ease;
  }
  @keyframes pulse {
    from {
      transform: scale(0.8);
    }
    to {
      transform: scale(1);
    }
  }
</style>



