import storage from "../js/util/storage";
import dataJS from "../js/util/data";
class UIPrefs {
  constructor() {
    this.storage = new storage();
    // Set default prefs
    this.defaultPrefs();
  }

  // To get UI prefs based on project ID
  async getPrefs() {
    if (this.storage.getSessionStorage(`prefs-${window.flowID}`)) return (this.prefs = await this.storage.getSessionStorage(`prefs-${window.flowID}`));
    var data = new dataJS("https://botup.${region}.500apps.com/botup/uiprefs");
    var response = await data.get();

    // Check prefs is found in DB or not
    response.ui_prefs ? this.setPrefs(response) : this.defaultPrefs(true);
  }

  /**
   * Set DB prefs to widget
   * @param {Object} response UI prefs from DB
   */
  setPrefs(response) {
    var DBPrefs = JSON.parse(response.ui_prefs);
    this.prefs.widget = DBPrefs.widget;
    this.prefs.chat_header.css.background = `${DBPrefs.bgcolor} !important`;
    this.prefs.chat_header.attr.gravatar = DBPrefs.pic_path;
    this.prefs.chat_header.attr.bot_name = DBPrefs.name;
    this.prefs.chat_header.attr.bot_description = DBPrefs.description;
    this.prefs.chat_header.attr.bot_welcome_msg = DBPrefs.message;
    this.prefs.chat.attr.transition = DBPrefs.transition;
    DBPrefs.color ? (this.prefs.chat_body.css.background = DBPrefs.color) : (this.prefs.chat_body.css.backgroundImage = `url(${DBPrefs.bgImage})`);
    this.prefs.font_family.css.fontFamily = `${DBPrefs.fontFamily} !important`;
    this.prefs.chat_body.css.fontSize = `${DBPrefs.fontSize}px !important`;
    this.prefs.chat_width.css.width = `${DBPrefs.width}px !important`;
    this.prefs.chat_body.css.height = `max-height:${DBPrefs.height}px !important`;
    this.prefs.chat.sound.playMessageIn = `https://infinity.500apps.com${DBPrefs.playMessageIn}`;
    this.prefs.chat.sound.playMessageOut = `https://infinity.500apps.com${DBPrefs.playMessageOut}`;
    this.storage.setSessionStorage(`prefs-${window.flowID}`, this.prefs);
  }

  // Set default prefs
  defaultPrefs(flag) {
    this.prefs = {
      widget: "ultra",
      chat_icon: {
        attr: {
          active_image: "https://infinity.500apps.com/img/botup/cross.svg",
          chat_image: "https://infinity.500apps.com/img/botup/chatly.svg",
        },
      },
      chat_header: {
        css: {
          background: "linear-gradient(92deg, #4C00C9 0%, #1B17CF 100%) !important",
        },
        attr: {
          gravatar: "https://infinity.500apps.com/img/botup/bot.svg",
          welcome_img: "https://infinity.500apps.com/img/botup/hand.png",
          bot_name: "Bot by 500apps",
          bot_description: "Let's chat. We are here",
          bot_welcome_msg: "Hi! I am bot working for 500apps, I can help you with answering a few questions.",
        },
      },
      chat: {
        attr: { transition: "botup-animation-fade-in-up" },
        sound: { playMessageIn: "https://infinity.500apps.com/img/botup/audios/botdefault.mp3", playMessageOut: "https://infinity.500apps.com/img/botup/audios/userdefault.mp3" },
      },
      chat_width: {
        css: {
          width: "360px !important",
        },
      },
      chat_body: {
        css: {
          fontSize: "14px !important",
          backgroundImage: "url(https://infinity.500apps.com/img/botup/chat-board-bg.png)",
          maxHeight: "300px !important",
        },
      },
      font_family: {
        css: {
          fontFamily: " 'Open Sans', sans-serif !important",
        },
        attr: {},
      },
    };
    if (!flag) return;
    this.storage.setSessionStorage(`prefs-${window.flowID}`, this.prefs);
  }

  /**
   * Load CSS
   * @param {String} src Respective theme
   * @param {String} id Theme id
   */
  loadCSS(src, id) {
    var fileref = document.createElement("link");
    fileref.rel = "stylesheet";
    fileref.type = "text/css";
    fileref.href = src;
    if (id) fileref.id = id;
    document.getElementsByTagName("head")[0].appendChild(fileref);
  }

  /**
   * Play respective tone
   * @param {mp3} tone Get respective tone value
   */
  play(tone) {
    try {
      let audio = new Audio(tone);
      audio.play();
    } catch (error) {
      console.log("Audio is not supported for older versions of browser");
    }
  }
}

export default UIPrefs;



