import Util from "./util/main";
import Bot from "./bot/flow";
import State from "./core/state";
import Chat from "./chat/send";
import UIPrefs from "../css/prefs";
import store from "./bot/store";

class Botup {
  constructor() {
    this.Util = Util;
    this.Bot = new Bot();
    this.State = State;
    this.Chat = new Chat();
    this.UIPrefs = new UIPrefs();
    this.store = new store();
    // Create storage object
    this.storage = new this.Util.storage();
    this.storage.setSessionStorage("chat", "Inactive");
  }

  /**
   * Entry point of script which given by user
   * @param {Number} apiKey  Invoke API KEY from website
   * @param {Number} flowID  Message which is given by user
   */
  async init(apiKey, flowID, region) {
    // Create session object
    var session = new this.Util.session();

    window.refresh_loading = false;
    // Get admin inputs from state
    window.flowID = window._Botup.State.getters.getBotConfig.flowID;

    // To remove active conversation from vuex when user change flow
    if (window.flowID !== flowID) {
      window._Botup.State.commit("removeActiveConversation", []);
      this.storage.removeItem("conversationID");
      this.storage.removeItem(`botup-${window.flowID}`);
      window._Botup.State.commit("removeUserInputs", {});
    }

    window.flowID = flowID;
    window.apiKey = apiKey;
    window.region = region;

    // Save admin inputs in state
    window._Botup.State.commit("botConfig", { flowID: flowID, apiKey: apiKey, region });

    // Generate visitor information county,IP etc..
    var visitorInfo = session.getVisitorInfo();

    // Generate visitor ID with respective website
    session.getVisitorID((id) => {
      this.visitorID = id;
    });

    // Generate active conversation ID
    this.Chat.conversation.getCurrentActiveConversationID();

    // Get flow JSON based on website which is from script
    var botJson = await window._Botup.Bot.getBot(window.flowID);

    // Get prefs based on project ID
    await window._Botup.UIPrefs.getPrefs();

    // Inject respective theme css based on DB prefs
    window._Botup.UIPrefs.loadCSS(`https://widgets.500apps.com/botup/CSS/${window._Botup.UIPrefs.prefs.widget}.css`, `500apps-${window._Botup.UIPrefs.prefs.widget}`);

    // Read step from local storage if this is executing again
    var stepId = this.storage.getLocalStorage(`botup-${window.flowID}`);

    // Get active message from vuex
    let stateJSON = this.State.getters.getActiveMessage;

    // Flow will be start after visitor initiate the message
    this.State.watch(
      (state) => state.activeMessage,
      (stateJSON) => {
        if (this.storage.getLocalStorage(`botup-${window.flowID}`) && this.storage.getSessionStorage("chat") == "Inactive") {
          botJson = window._Botup.State.getters.getExecJson;
          var stepJson = botJson.filter((item) => {
            if (item.id == stepId) return item;
          });
          window._Botup.Bot.execEachStep(stepJson[0]);
        }
        if (this.storage.getLocalStorage(`botup-${window.flowID}`)) {
          // Check the name and email given or not
          if (!window._Botup.State.getters.getGeneratedKey && Object.keys(window._Botup.State.state.email).length > 0 && Object.keys(window._Botup.State.state.name).length > 0 && Object.keys(window._Botup.State.state.phoneNumber).length > 0) {
            // To store visitor details
            this.store.contactInfo();
          }
          return;
        }
        botJson = this.storage.getSessionStorage(`flowID-${window.flowID}`);
        this.storage.setSessionStorage("chat", "Active");
        // Conversation set to empty when start the flow
        this.State.state.conversations = [];
        /**
         * Execution Starts
         * @param {JSON} stepId  active step ID
         * @param {Number} botJson  call flow JSON
         * @param {Number} stateJSON  Message which is given by visitor
         */
        window._Botup.Bot.executeFlow(stepId, botJson, stateJSON);
      },
    );
  }
}
export default Botup;



